<template>
<div class="adminContainer U-margin-top-md">


    <div class="headerBox">
<h1 class="heading-5 headerBox__head">Welcome Solomon</h1>
    </div>
    <div class="errorBox">
<p v-for="error in errors" :key="error" class=" small-text invalidInputText admin-head">{{error}}</p>
    </div>

    <div class="formBox">
    <form class="form adminForm">

      <div  class="form__group">
        <label for="email" class="form__label heading-5 adminForm__label">Email</label>
        <input :class="{invalidTnputBox: invalidInput}" type="email" v-model="email"  name="email" id="email" class="form__input adminForm__input">

      </div>
      <div class="form__group">
        <label for="password" class="form__label adminForm__label">Password</label>
        <input :class="{invalidTnputBox: invalidInput}" type="password" v-model="pwd"  name="password" id="password" class="form__input adminForm__input">

      </div>
      <div class="form__group">
        <button @click.prevent="login" class="btn btn--green">Login</button>
      </div>
      <div class="form__group">
        <a @click="goToReset" class="small-text cursor-pointer">Forgot Password? &rarr;</a>
      </div>
    </form>
    </div>








</div>
</template>

<script>

import axios from 'axios'
import { mapActions, mapGetters } from 'vuex'

export default {

components: {

},

data(){
  return{

 url: '/admin/',
  invalidInput: false,
 admin: [],
 adminDetail: [],
 email: null,
 pwd: null,
 pTitle: '',
 pDescription: '',
 errors: [],
 showLogin: true,
  myToken: null,
  tokens: null,
  rejex: /\S+@\S+\.\S+/
  }
},
computed: {
...mapGetters({
  getStateToken: 'getStateToken'

})

},
methods: {

  ...mapActions({
    updateAdmin: 'updateAdmin',
    getAccessToken: 'getAccessToken',

  }),
  login(){
this.errors = []

axios.get(this.url)
        .then((res) => {
          this.admin = res.data.adminDetails
          for (let allAdmin of this.admin) {

            setTimeout(() => {
  this.invalidInput = false;

}, 1000, this.validateForm());

this.email = this.email.toLowerCase();

   if(!this.rejex.test(this.email)){
    this.errors.push('Invalid Email Format')
     this.invalidInput = true
  }

  if( this.email && this.pwd && this.rejex.test(this.email) && this.email != allAdmin.email && this.pwd != allAdmin.password){
    this.errors.push('Wrong Login Details')
     this.invalidInput = true
  }
  if( this.email && this.pwd && this.rejex.test(this.email) && this.email === allAdmin.email && this.pwd != allAdmin.password){
    this.errors.push('Wrong Login Details')
     this.invalidInput = true
  }
  if( this.email && this.pwd && this.rejex.test(this.email) && this.email != allAdmin.email && this.pwd === allAdmin.password){
    this.errors.push('Wrong Login Details')
     this.invalidInput = true
  }

  if (!this.errors.length && this.email === allAdmin.email && this.pwd === allAdmin.password) {
        this.tokens = res.data.token
          localStorage.setItem('tokens', this.tokens)
     this.getAccessToken(this.tokens)

     this.$router.push({name: 'dashboard'})
        return true;
      }
          }
        })
        .catch(err => {
          console.log(err.message)
        })



  },
  validateForm(){


    if(!this.email && this.pwd){
    this.errors.push('Error email field empty')
    this.invalidInput = true
  }if(!this.pwd && this.email){
    this.errors.push('Error password field empty')
     this.invalidInput = true
  }
  if(!this.pwd && !this.email){
    this.errors.push('Error both fields empty!')
     this.invalidInput = true
  }




  },
  goToReset(){
    this.$router.push({name: 'resetPage'})
  }

},
created(){




},
mounted(){

},



}
</script>

