import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Admin from './views/Admin'
import ResetPage from './views/ResetPassword'
import Dashboard from './views/DashBoard'
import Blog from './views/Blog'
import SelectedBlog from './views/SelectedBlogView'
import Contact from './views/Contact'
import Fights from './views/Fights'
import { store } from './store/store'

Vue.use(Router)
Vue.use(store)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,

  routes: [
    {
      path: '/',
      component: Home,
      name: 'home'
    },

    {
      path: '/admin',
      name: 'admin',
      component: Admin
    },
    {
      path: '/blog',
      name: 'blog',
      component: Blog

    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: Dashboard

    },
    {
      path: '/fights',
      name: 'fights',
      component: Fights
    },
    {
      path: '/myBlog',
      name: 'myBlog',
      component: SelectedBlog
    },
    {
      path: '/resetPassword',
      name: 'resetPage',
      component: ResetPage
    },
    {
      path: '*',
      component: Home
    }
  ]
})
