export default {

  state: {
    SelectedBlog: null

  },

  mutations: {
    mutateBlogDetails: (state, payload) => {
      state.SelectedBlog = payload
    }
  },

  actions: {
    modifyBlog: ({ commit }, payload) => {
      commit('mutateBlogDetails', payload)
    }
  },
  getters: {
    getSelectedBlog: state => {
      return state.SelectedBlog
    }
  }
}
