
export default {

  state: {
    showMyPost: true,
    showControl: false,
    myPostTitle: null,
    myPostDescription: null,
    myPostdescImg: null,
    postList: true

  },

  mutations: {
    mutateControl: (state) => {
      state.showControl = !state.showControl
    },
    mutatePosts: (state) => {
      state.showMyPost = !state.showMyPost
    },
    refreshMutate: (state) => {
      state.showMyPost = true
    },

    mutatePostTitle: (state, pTitle) => {
      state.myPostTitle = pTitle
    },
    myPostDescription: (state, pDesc) => {
      state.myPostDescription = pDesc
    },
    mutatePostList: (state, payload) => {
      state.postList = payload
    }
  },
  actions: {

    changeControl: ({ commit }) => {
      commit('mutateControl')
    },
    changePosts: ({ commit }) => {
      commit('mutatePosts')
    },
    refreshLists: ({ commit }) => {
      commit('refreshMutate')
    },
    modifyPostTitle: ({ commit }, pTitle) => {
      commit('mutatePostTitle', pTitle)
    },
    modifyPostDescription: ({ commit }, pDesc) => {
      commit('myPostDescription', pDesc)
    },
    changePostList: ({ commit }, payload) => {
      commit('mutatePostList', payload)
    }
  },
  getters: {
    getPostTitle: state => {
      return state.myPostTitle
    },
    getPostDescription: state => {
      return state.myPostDescription
    },
    getPostList: state => {
      return state.postList
    },
    showControl: state => {
      return state.showControl
    },
    showMyPost: state => {
      return state.showMyPost
    }
  }

}
