<template>
  <div class="footerComponent">
<div class="footerFormBox">
  <p class="paragraph-1">{{formText}}</p>
<form ref="cta" v-if="hideFooterForm" class="footer-cta form ">
<div class="form__group">
<button @click="goToContact()" v-scroll-to="'#cta'" class="btn  btn--green-outline">Contact Me</button>
</div>
  </form>


  <form ref="cta" v-else action="" class="footer-cta form form--inline-3">

    <div class="form__group  ctaForm__group">
        <input :class="{invalidTnputBox: invalidInput}" v-model="name" type="text" class="form__input " placeholder="Full Name">
      </div>
      <div class="form__group  ctaForm__group">

        <input :class="{invalidTnputBox: invalidInput}" v-model="email" type="text" class="form__input " placeholder="Email">
      </div>
      <div class="form__group  ctaForm__group">

        <button @click.prevent="subscribe()" class="btn btn--green-outline ctaForm__btn">Subscribe</button>
      </div>
<div v-for="error of errors" :key="error" class="error" >{{error}}</div>
  </form>
</div>

  <div class="footer-social">
    <p class=" footer-social__header">
      Follow me on
    </p>
    <div class="footer-social__box">
<i class="fab fa-facebook footer-social__box--icon "></i>
    <i class="fab fa-instagram footer-social__box--icon"></i>
    <i class="fab fa-youtube footer-social__box--icon"></i>
    </div>
  </div>
  <p class="footer-copyright">Copyright © by FindSamuels 2019</p>
</div>

</template>

<script>
import axios from 'axios'
import { mapActions } from 'vuex';
export default {
  components: {

  },
  data(){
    return{
      hideFooterForm: true,
      formText: '',
      name: null,
email: null,
submitStatus: null,
invalidInput: false,
errors: [],
subscribeUrl: '/subscribe/',
 rejex: /\S+@\S+\.\S+/,
    }
  },
  methods:{
...mapActions({
  ChangefooterKey: 'ChangefooterKey'
}),
    checkRoute(){
      if(this.$route.name === 'contact'){
this.hideFooterForm = false;
this.formText = 'Subscribe to recieve updates'
      }
else{
  this.formText = 'Get In Touch Today'
}
    },
    goToContact(){
      if(this.$route.name === 'contact'){
this.$router.push({name: 'home '})

      }
      else{
        this.$router.push({name: 'contact'})
      }

    },
    validation(){
    this.errors = [];
    if(!this.name){
       this.invalidInput = true;
      this.errors.push('Name required!')

    }
    if(!this.email){
      this.invalidInput = true;
      this.errors.push('Email required!')

    }
    if(this.email && !this.rejex.test(this.email)){
      this.invalidInput = true
    this.errors.push('Invalid Email Format')

  }


  },
    subscribe(){
      setTimeout(() => {

      this.invalidInput = false;

      }, 1000,this.validation());

if(!this.errors.length){
      axios.post(this.subscribeUrl, {
          name: this.name,
          email: this.email
        })
        .then((res) => {
          console.log('Subscribed')

        })
        .catch((err) => {
          console.log(err + 'error with email')
        })
this.ChangefooterKey();
}


    }
  },
  created(){
    this.checkRoute();
  }
}
</script>

