<template>
<div class="g-container">
  <div class="adminBase">
<div class="leftBar adminControl " :class="{showDropdown: showControl}">

    <ul class="leftBar__item">
<div class="potraitBox">
        <img src="img/CreateImg.jpg" alt="" class="potraitBox__img">
        <p class="potraitBox__caption "> Solomon King Simon </p>
      </div>

       <li class="fas fa-home leftBar__controlIcon leftBar__list" @click="AdminDashboard(); changeControl(); changeIcon();">&nbsp;Dashboard</li>
        <li class="fas fa-plus leftBar__controlIcon leftBar__list" @click="CreatePosts(); changeControl(); changeIcon();">&nbsp;Create Posts</li>
         <li class="fas fa-eye leftBar__controlIcon leftBar__list" @click="ViewList(); changeControl(); refreshLists(); changeIcon();" >&nbsp;Modify Posts</li>
         <li class="fas fa-eye leftBar__controlIcon leftBar__list" @click="AddVideo(); changeControl(); refreshLists(); changeIcon();" >&nbsp;Add Video</li>
          <li class="fas fa-user-cog leftBar__controlIcon leftBar__list" @click=" DeleteVideo(); changeControl(); refreshLists(); changeIcon();">&nbsp;Delete Videos</li>

    </ul>


  </div>

  <div class="adminItems">
    <component :is="selectedComponent">


    </component>
  </div>
  </div>


</div>

</template>

<script>
import CreatePost from './CreatePost'
import ModifyPost from './ModifyPost'
import AddVideo from './AddVideos'
import DeleteVideo from './DeleteVideo'
import AdminViewBase from './AdminViewBase'
import { mapActions, mapGetters } from 'vuex'
export default {
  props: {
defaultIcon: Boolean
  },
components: {
CreatePost,
ModifyPost,
AdminViewBase,
AddVideo,
DeleteVideo


},
  data(){
    return{
       selectedComponent: AdminViewBase,
    hideDropdown: null,
    refreshPost: this.showPosts
    }
  },
  computed:{
      ...mapGetters({
        showControl: 'showControl',
        showPosts: 'showMyPost',
        getpostKey: 'getpostKey',

      })
  },
  methods: {
    ...mapActions({
      changeControl: 'changeControl',
      refreshLists: 'refreshLists',
       changeIcon: 'CHANGEICON'
    }),
    ViewList(){

      this.selectedComponent = ModifyPost


    },
    AddVideo(){
this.selectedComponent = AddVideo
    },
    DeleteVideo(){
this.selectedComponent = DeleteVideo
    },
    CreatePosts(){
      this.selectedComponent = CreatePost

    },
    AdminDashboard(){
this.selectedComponent = AdminViewBase

    }
  }

}
</script>
