<template>
  <div class="deleteComponent">
<h1 class="styledHeader-1">Select a video to delete</h1>

<form action="" class="form deleteComponent__postSearch">
   <div class="form__group">
     <input v-model="search" type="text" placeholder="Search for video" class="form__input blueInputBox ">
   </div>
 </form>
<div class="videoBox">
  <div v-for="fight of filterList" :key="fight._id" class="videoList">
    <h4 class="medium-text videoList__title">{{fight.fightTitle}}

    </h4>
<i @click="openDeleteBox(fight._id)" class="far fa-trash-alt videoList__delete"></i>
<div v-if="showDelete" class="deleteBox">
  <h4 class=" deleteBox__header">Continue to delete?</h4>
  <div class="deleteBox__deleteButtons">
  <button @click.prevent="cancel" class="btn btn--green">No</button>
  <button @click.prevent="deleteVideo(selectedVideo._id)"  class="btn btn--red">Yes</button>
  </div>

</div>
  </div>

</div>

  </div>
</template>

<script>
import axios from 'axios'
import { mapActions } from 'vuex'
export default {

data(){
  return{
    urlVideo: '/video/',
    // urlVideo: 'http://localhost:3000/video/',
    fights: [],
    showDelete: false,
    selectedVideo: null,
    search: '',
  }
},
computed: {
  filterList(){
        if(this.search){
      return this.fights.filter((fight)=>{
        return fight.fightTitle.toLowerCase().startsWith(this.search);
      })
      }else{
        return this.fights;
      }

      }
},
methods: {
  ...mapActions({
ChangepostKey: 'ChangepostKey'
  }),
  openDeleteBox(id){
    axios.get(`${this.urlVideo}${id}`, {
      params: {
        _id: id
      }
    })
    .then((response) => {
      this.selectedVideo = response.data.videoDetails
      this.showDelete = true;

      console.log(this.selectedVideo)
    })
    .catch(err => {
      console.log(err + ' Cannot find video')
    })

  },
  cancel(){
this.showDelete = false;
  },
  getVideos(){
    axios.get(this.urlVideo)
    .then((res)=>{
      this.fights = res.data.videoDetails
    })
  },
  async deleteVideo(id){
    await axios.delete(`${this.urlVideo}${id}`)
    .then((res) => {


      this.showDelete = false;
    })
    .catch((err) => {
      console.log(err + ' Error occured')
    })
    this.ChangepostKey();
  }
},
created(){
  this.getVideos();
}
}
</script>
