export default {

  state: {
    defaultIcon: true
  },

  mutations: {
    mutateIcon: (state, payload) => {
      state.defaultIcon = !state.defaultIcon
    }
  },
  actions: {
    CHANGEICON: ({ commit }) => {
      commit('mutateIcon')
    }
  },
  getters: {
    getIconControl: state => {
      return state.defaultIcon
    }
  }

}
