export default {

  state: {
    contactKey: 0,
    postKey: 0,
    subscribeKey: 0,
    footerKey: 0

  },

  mutations: {
    mutatecontactKey: (state) => {
      state.contactKey += 1
    },
    mutatepostKey: (state) => {
      state.postKey += 1
    },
    mutatesubscribeKey: (state) => {
      state.subscribeKey += 1
    },
    mutatefooterKey: (state) => {
      state.footerKey += 1
    }
  },

  actions: {
    ChangecontactKey: ({ commit }) => {
      commit('mutatecontactKey')
    },
    ChangepostKey: ({ commit }) => {
      commit('mutatepostKey')
    },
    ChangesubscribeKey: ({ commit }) => {
      commit('mutatesubscribeKey')
    },
    ChangefooterKey: ({ commit }) => {
      commit('mutatefooterKey')
    }
  },
  getters: {
    getcontactKey: state => {
      return state.contactKey
    },
    getpostKey: state => {
      return state.postKey
    },
    getsubscribeKey: state => {
      return state.subscribeKey
    },
    getfooterKey: state => {
      return state.footerKey
    }
  }

}
