<template>
  <div class="videoComponent">
    <h1 class="heading-3">Upload or Paste video link</h1>
    <form action="" method="POST" enctype="multipart/form-data" class="form form-inline">
      <div class="form__group">
        <input :maxlength="maxTitle" v-model="videoTitle" type="text" class="form__input" placeholder="Title">
      </div>




<div class="form__group">
   <div class="vidContainer">
        <input v-model="videoId" type="text" class="form__input" placeholder="Paste video url here">
<button @click.prevent="checkVideo" class="btn btn--green btn--animate">Upload</button>
        </div>
</div>
<div v-if="validVideo != ''" class="form__group">
      <iframe class="uploadedVideo" ref="myId" width="560" height="315" :src="validVideo" frameborder="0" allowfullscreen></iframe>
</div>

 <div class="form__group">
        <button @click.prevent="postVideo" class="btn btn--blue btn--animate">Submit</button>
      </div>

    </form>
  </div>
</template>

<script>
import axios from 'axios'
export default {

  data(){
return{
result: '',
videoTitle: '',
resultMethod: '',
maxTitle: '40',
videoId: '',
validVideo: '',
urlVideo: 'http://localhost:3000/video/',
// urlVideo: '/video/',
regExp: /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/,
match: '',
}
  },
  methods: {
checkVideo(){

this.match = this.videoId.match(this.regExp);
if(this.match && this.match[2].length == 11){
this.validVideo = `//www.youtube.com/embed/${this.match[2]}`
}
},
async postVideo(){

this.match = this.videoId.match(this.regExp);

  await axios.post(this.urlVideo, {
    fightTitle: this.videoTitle,
    fightResult: this.result,
    resultMethod: this.resultMethod,
    videoId: this.match[2]
  })
  .then((res) => {
    console.log('Video Added from form')
  })
  .catch(err => {
    console.log( err + 'Cannot upload Video')
  })
}
  }
}
</script>

