<template>
  <div class="latestPosts">
<h2 class="styledHeader-1 heading-2">Latest Posts</h2>
<div class="latestPostBox">

   <div v-scroll-reveal.reset v-for="(myRecentPost, index) of recentPost " :key="index" class="card blogList__card  ">
    <template v-if="myRecentPost.postObject != undefined || myRecentPost.postObject != null">
      <img :src="myRecentPost.postObject" class="card__img blogList__img "   alt="" srcset="">
    </template>

    <template v-else>
<img class="card__img blogList__img "  :src="defaultImg " alt="" srcset="">
    </template>


    <div class="card__header ">
      <h3 class="blogList__header  ">{{myRecentPost.postTitle}} </h3>
     </div>
    <div class="card__title blogList__title">
      <p v-html="myRecentPost.postDescription.substring(0, 60) + '...'"></p>
    </div>
    <div class="blogList__items">
<button @click="goToPost(myRecentPost._id)" class="btn btn--animate readmore">Read More &rarr;</button>
<p class="blogList__createDate small-text">{{myRecentPost.dateCreated}}</p>

    </div>

  </div>
</div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapActions } from 'vuex';
export default {
components:{

},
data(){
  return{
    url: '/post/',
    recentPost: [],
    defaultImg: 'img/CreateImg.jpg'
  }
},
methods: {
  ...mapActions({
modifyBlog: 'modifyBlog'
  }),
  getRecentPosts(){
 axios.get(this.url)
.then((res) => {
this.recentPost = res.data.postDetails
this.recentPost = this.recentPost.reverse();
this.recentPost = this.recentPost.slice(0, 4)
})
.catch((err) => {
  console.log(err.message)
})
},

goToPost(id){

    axios.get(`${this.url}${id}`, {
      params: {
        _id: id
      }
    })
    .then((res) => {

this.modifyBlog(res.data.postDetails)
localStorage.setItem('blogs', JSON.stringify(res.data.postDetails))

this.$router.push({name: 'myBlog'})

    })
    .catch(err =>{
      console.log(err + 'Posts not available')
    })

  },
},
created(){
this.getRecentPosts();
}
}
</script>

