<h2 class="heading-4 contactInfo__heading">Promotional Opportunity</h2>
<p class="contact-description">Lorem ipsum, dolor sit amet consectetur adipisicing elit. Unde eaque ducimus debitis temporibus optio ipsum tempore, in sequi facilis culpa, nulla eos obcaecati! Consequatur voluptates ducimus vitae magnam quibusdam qui.</p>
