</div>




<div class="contactForm">
  <h2 class="heading-5 contactForm__heading">Fill out the form below. <br><span class="small-text contactForm__text"> Contact me directly on 089-XXX-XXX <br> Email: <span class="email">Solomonkingsimonblog@gmail.com</span></span></h2>

<form ref="contactForm" action="" class="form contactForm__form">

  <div class="form__group ">

    <input :class="{invalidTnputBox: invalidName}"  v-model="name" type="text" class="form__input" placeholder="Name">
    <div class="error" >{{checkName}}</div>
  </div>
  <div class="form__group ">

    <input :class="{invalidTnputBox: invalidEmail}"  v-model="emailAddress" type="text" class="form__input" placeholder="Email">
    <div class="error" >{{checkEmail}}</div>
  </div>
  <div class="form__group ">

    <input :class="{invalidTnputBox: invalidNumber}"  v-model="mobileNumber" type="number" class="form__input" placeholder="Mobile Number">
    <div class="error" >{{checkNumber}}</div>
  </div>
  <div class="form__group ">

  <textarea :class="{invalidTnputBox: invalidMessage}"  v-model="message" class="form__input" name="message" id="" cols="30" rows="10" placeholder="Message*"></textarea>
  <div  class="error" >{{checkMessage}}</div>
  </div>

  <div class="form__group">
    <button @click.prevent="submitForm($event)" class="btn btn--blue">Submit</button>
  </div>
</form>
</div>

</div>

    </div>


</template>

<script>
import axios from 'axios'
import { mapActions, mapGetters } from 'vuex';
export default {

data(){
  return{

    checkName: null,
    checkEmail: null,
    checkNumber: null,
    checkMessage: null,
    name: null,
    mobileNumber: null,
    phoneno: /^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/,
    message: null,
    emailAddress: null,
    invalidName: false,
    invalidNumber: false,
    invalidEmail: false,
    invalidMessage: false,
    errors: [],
    contactUrl: '/contact/',
     rejex: /\S+@\S+\.\S+/,
  }
},
methods: {
  ...mapActions({
    ChangecontactKey: 'ChangecontactKey'
  }),
  validation(event){
    this.errors = [];


    if(!this.name){
       this.invalidName = true;
      this.checkName = 'Name required!'

    }

    if(!this.emailAddress){
      this.invalidEmail = true;
      this.checkEmail = 'Email required!'

    }
    if(!this.mobileNumber){
      this.invalidNumber = true;
      this.checkNumber='Mobile Number required!'

    }
    if(this.mobileNumber && !this.phoneno.test(this.mobileNumber)){
      this.invalidNumber = true;
      this.checkNumber='Invalid mobile number!'
    }

    if(!this.message){
      this.invalidMessage = true;
      this.checkMessage='Message required!'

    }
    if (this.emailAddress && !this.rejex.test(this.emailAddress)){
      this.invalidMessage = true
    this.checkEmail = 'Invalid Email Format'

  }





  },
  submitForm(e){
e.preventDefault();
let vm = this;

 setTimeout(() => {
      this.invalidMessage = false;
      this.invalidName = false;
      this.invalidNumber = false;
      this.invalidEmail = false;
       this.checkName = '',
      this.checkEmail = '',
      this.checkNumber = '',
      this.checkMessage = ''
    }, 1000,this.validation());
    if(this.name && this.emailAddress && this.message && this.mobileNumber & this.rejex.test(this.emailAddress) ){
axios.post(this.contactUrl, {
      name: this.name,
      mobileNumber: this.mobileNumber,
      message: this.message,
      emailAddress: this.emailAddress,
    })
    .then((res) => {

console.log( res + 'Mesage Sent')
    })
    .catch(err => {
      console.log(err)
    })

console.log('Mesage Sent')
vm.ChangecontactKey();
console.log(vm.getcontactKey)

    }







  this.$refs.contactForm.reset();
  },

},
computed: {
  ...mapGetters({
    getcontactKey: 'getcontactKey'
  })
}
}
</script>

