<template>
  <div class="CreateComponent ">
    <h1 class="heading-4 CreateComponent__head">Create New Post</h1>
    <div class="createFormBox">
      <div class="errorBox">
<p v-for="error in errors" :key="error" class=" small-text invalidInputText admin-head">{{error}}</p>
    </div>
<form ref="imageUpload" method="POST" enctype="multipart/form-data" action="#" class="form">


      <div class="form__group">
        <!-- <label for="postTitle" class="form__label">Post Title</label> -->
        <input  v-model="postTitle" :maxlength="maxTitle"  type="text" name="postTitle" class="form__input" placeholder="Enter Blog Title Here">
      </div>
      <div class="form__group">
        <!-- <label for="postTitle" class="form__label">Post Title</label> -->
        <input  v-model="postDescription" :maxlength="maxDescription" type="text" name="postTitle" class="form__input" placeholder="Enter Blog Description Here">
      </div>

      <div class="form__group">

        <VueEditor
        class="form__input whiteBackground"
        name="postDescription"

        id="postDescription"
        v-model="postContent"
        :config="config" />



        <!-- <textarea v-model="postDescription" name="postDescription" id="postDescription" cols="30" rows="10" class="form__input"></textarea> -->
      </div>
      <div class="form__group">
        <label for="blogImage" class="form__label small-text" >Preview image</label>
        <input
        @change="imageUpload"
        ref="file"
        type="file"
        name= "file"
        class="form__input"/>
      </div>


      <div class="card">
        <img :src="imageInfo" alt="" class="card__img" v-if="imageInfo.length > 0">
      </div>
<h4 class="small-text">{{message}}</h4>
      <div class="form__group">
        <button @click.prevent="addPost" class="btn btn--green">Publish</button>

      </div>
    </form>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
import { VueEditor } from 'vue2-editor'
import VueFroala from 'vue-froala-wysiwyg'
import { mapActions } from 'vuex';
export default {
components: {
VueEditor
},
data(){

  return{

 urlPost: '/post/' ,
 postTitle: null,
 postDescription: null,
 postContent: null,
 imageObject: null,
 message: '',
file: null,
editorData: '',
imageInfo: '',
maxTitle: '40',
maxDescription: '100',
subscribeUrl: '/subscribe/',
addedPost: null,
errors: [],
config: {
        events: {
          'froalaEditor.initialized': function () {
            console.log('initialized')
          }
        }
      },


  }
},



methods: {
  ...mapActions({
ChangepostKey: 'ChangepostKey'
  }),
imageUpload(event){
this.file = event.target.files[0];
console.log(this.file)
this.imageInfo = URL.createObjectURL(this.file)


},
// checkLength(descLength){
//   this.errors = []
// if(descLength.length >= 100){
//   this.errors.push = 'Maximum characters is 100'
//   console.log(descLength.length)
// }

 addPost(){
   let formData = new FormData();
formData.append('file', this.file)

formData.append('postTitle',this.postTitle )
formData.append('postDescription', this.postDescription  )
formData.append('postContent', this.postContent )

  axios.post(this.urlPost, formData)
   .then((response) => {
      this.message = 'Form Submited'

return axios.post(this.subscribeUrl, {
        latestPostTitle: this.postTitle
      })
      .then((res) => {
console.log(res, 'Sent')
      })
      .catch((err) => {
console.log(err + ' Cannot send this')
console.log('post tittle:' + this.postTitle)
      })
    })
    .catch((err) => {
      console.log(err),
      this.message= 'Something went wrong with post'
    })
this.ChangepostKey();



},
 async sendMail(){

}

},
created(){

}
}
</script>

