
export default {

  state: {
    AllAdmins: {
      username: null,
      password: null
    },
    loggedIn: false,
    Statetoken: null,
    status: ''

  },

  mutations: {
    mutateLogout: (state) => {
      state.Statetoken = null
      localStorage.removeItem('tokens')
    },
    updateAccessToken: (state, token) => {
      state.Statetoken = token
    }
  },
  actions: {
    Dologout: ({ commit }) => {
      commit('mutateLogout')
    },
    getAccessToken: ({ commit }) => {
      commit('updateAccessToken', localStorage.getItem('tokens'))
    }
  },
  getters: {
    getUsername: state => {
      return state.AllAdmins.username
    },
    getPassword: state => {
      return state.AllAdmins.password
    },
    getStateToken: state => {
      return state.Statetoken
    }
  }
}
