<template>
  <div class="banner">
    <div class="banner__box">
<h1 class="banner__header ">{{bannerHeader}}</h1>
    <p class="banner__text ">{{bannerText}}</p>

    </div>

  </div>
</template>

<script>
export default {
  components: {

  },
  data(){
return {
bannerHeader: '',
bannerText: ''
}
  },
  methods: {
    dynamicBanner(){
      if(this.$route.name === 'contact'){
        this.bannerHeader = 'Contact'
        this.bannerText = 'Looking forward to speaking to you!'
      }
      if(this.$route.name === 'blog'){
        this.bannerHeader = 'My Journey'
        this.bannerText = 'Always something interesting here'
      }
      if(this.$route.name === 'fights'){
        this.bannerHeader = 'Fights'
        this.bannerText = 'Watch all my fights videos here'
      }
    }
  },
  computed:{

  },

  created(){
    this.dynamicBanner();
  }

}
</script>

