<template>
  <div class="fightComponent U-margin-md">
<!-- <h2 class="fightHeader styledHeader-1">Fights Of Solomon King Simon</h2> -->
 <paginate
name="myFights"
:list="uploadedVideo"
:per="4"

ref="paginator"
>
<div  class="fightContainer">



<div v-scroll-reveal.reset v-for="videos of paginated('myFights')" :key="videos._id" class="fightContainer__fights">
  <div class="card-1">

<iframe class="card-1__img"  ref="myId" width="560" height="315" :src="`//www.youtube.com/embed/${videos.videoId}` " frameborder="0" allowfullscreen></iframe>

    <p class="card-1__caption medium-text">{{videos.fightTitle}}</p>


  </div>
  </div>

</div>
</paginate>
<div class="paginatelinkBox">
  <span class="paginateInfo" v-if="$refs.paginator">
  Viewing {{$refs.paginator.pageItemsCount}} results
</span>
<paginate-links class="customisePaginate"   for="myFights"
:show-step-links="true"
:limit="2"
:hide-single-page="true"
 :step-links="{
    next: 'Next',
    prev: 'Prev'
  }"
></paginate-links>
  </div>
</div>

</template>

<script>
import axios from 'axios'
export default {
name: 'fightComponent',

data(){
  return{
// videoUrl: 'http://localhost:3000/video/',
videoUrl: '/video/',
uploadedVideo: [],
 paginate:['myFights'],


  }
},
methods: {
 getVideo(){
 axios.get(this.videoUrl)
.then((response) => {
this.uploadedVideo = response.data.videoDetails
this.uploadedVideo = this.uploadedVideo.reverse();
})
.catch((err) => {
  console.log(err)
})
}
},
created(){
this.getVideo();
console.log('created')
}
}
</script>

