<template>

    <nav class="nav" :class="[{customiseNav: controlNav}, {altNav, altNav}, {tallNav: showTallNav}]">
<router-view/>
      <div class="brand-box">
<router-link :to="{name: 'home'}" tag="li" > <a :class="{whiteText: altNav}" class="nav__brand heading-4">Solomon King Simon</a>  </router-link>
      </div>


    <div class="toggle-container" @click="toggleDropdownIcon">
      <i v-if="dafaultNavIcon" class="fas fa-bars nav__toggle"></i>
      <i v-else class="fas fa-times nav__toggle"></i>


    </div>

      <ul class="nav__item" :class="[{showNav: showItem}, {altItem: altNav}]">

        <router-link exact-active-class :to="{name: 'home'}" tag="li" class="nav__list" @click.native="toggleNav" ><a :class="{whiteText: altNav}" class="nav__link">Home</a></router-link>


<!-- <div  @click="toggleDropdown" class="nav__dropdown-box n">Media <span class="small-text icon-alignment"> <i class="fas fa-arrow-down "></i></span>
  <ul :class="{showDropdown: showDropdownItem}"  class="nav__dropdown-item">
    <router-link :to="{name: 'blog'}" tag="li" class="nav__list nav__dropdown-list" @click.native="toggleNav, changePostList(true)" ><a class="nav__link  nav__dropdown-list"> Blog</a></router-link>
<router-link :to="{name: 'video'}" tag="li" class="nav__list nav__dropdown-list" @click.native="toggleNav, changePostList(true)" ><a class="nav__link nav__dropdown-list">Videos</a></router-link>
  </ul>


</div> -->
 <router-link :to="{name: 'blog'}" tag="li" class="nav__list" @click.native="toggleNav, changePostList(true) " ><a :class="{whiteText: altNav}" class="nav__link">Blogs</a></router-link>
       <router-link :to="{name: 'fights'}" tag="li" class="nav__list" @click.native="toggleNav" ><a :class="{whiteText: altNav}" class="nav__link">Fights</a></router-link>
        <router-link :to="{name: 'contact'}" tag="li" class="nav__list" @click.native="toggleNav" ><a :class="{whiteText: altNav}" class="nav__link">Contact</a></router-link>
      </ul>

    </nav>


</template>

<script>
import { mapActions } from 'vuex';
export default {

data(){

  return{
scrolled: false,
showItem: false,
controlNav: false,
showDropdownItem: false,
altNav: false,
dafaultNavIcon: true,
showTallNav: false,
  }
},

methods: {
  showTallNav(){
if(this.showItem){
  this.showTallNav = true;
}
  },
  altNavControl(){
  if(this.$route.name === 'home'){
    this.changePostList(true)
    this.altNav = true;
  }
},
// changeIcon(){

//       this.dafaultNavIcon = !this.dafaultNavIcon

//   },
  ...mapActions({
changePostList: 'changePostList'
  }),
  handleScroll(){
this.scrolled = window.scrollY > 0;

if(this.scrolled){
this.controlNav = true;
}else{
  this.controlNav = false;
}
  },
  toggleNav(){

    this.showItem = !this.showItem
    this.dafaultNavIcon = !this.dafaultNavIcon;

  },
  toggleDropdownIcon(){
    this.showItem = !this.showItem;
    this.showTallNav = !this.showTallNav;
  }
},
created(){
  window.addEventListener('scroll', this.handleScroll);
  this.altNavControl();
  this.showTallNav();
}
}
</script>
