    <div class="blogList__items">
<button @click="goToPost(listPost._id)" class="btn btn--animate readmore small-text">Read More &rarr;</button>
<p class="blogList__createDate small-text">{{listPost.dateCreated}}</p>

    </div>

  </div>


</paginate>
