<template>
  <div class="viewComponent">


    <div class="viewComponent__title">
      <h1 class="heading-4 post-text">{{viewTitle}}</h1>
       <p class="heading-4 post-text">{{modifyInstructions}}</p>
    </div>


<template v-if="loading">
<div class="viewComponent__spinnerbox">
  <Myslot>
<template slot="spinner"></template>
  </Myslot>

</div>
 </template>

    <template  v-if="showPosts">
      <form action="" class="form viewComponent__postSearch">
   <div class="form__group">
     <input v-model="search" type="text" placeholder="Search Post" class="form__input blueInputBox ">
   </div>
 </form>
    <div class="viewComponent__postBox">
      <div v-for="post of filterList" :key="post._id" @click="getSelectedPost(post._id); changePosts()" class="postControl">
  <a class="viewComponent__lists">{{post.postTitle}}</a>
      </div>
    </div>
</template>


<template  v-else  >
  <div class="modifyFormBox">


  <form  action="#" class="form" >

      <div class="form__group">

        <input type="text" v-model="updateTitle" name="postTitle" :maxlength="maxTitle" class="form__input" placeholder="Edit Blog title here">
      </div>
       <div class="form__group">

        <input type="text" v-model="updateDescription" :maxlength="maxDescription" name="postTitle" class="form__input" placeholder="Edit Blog Description here">
      </div>
      <div class="form__group">

        <VueEditor name="postDescription"
        v-model="updateContent"
        id="postDescription"
        cols="30" rows="10" class="form__input whiteBackground"
        />
       </div>

      <div class="form__group">
        <label for="blogImage" class="form__label small-text" >Upload image</label>

        <div class="form__group">

        <input
        @change="updateImage"
        type="file"
        ref="file"
        :src="updatedImg"
         class="form__input">
          <Myslot v-if="loading">

        </Myslot>

</div>
      <template v-if="showUploadedImage">
<div class="card">
        <img :src="updatedImg" alt="" class="card__img" >
      </div>
      </template>

      <template v-else>
        <div class="card">
        <img :src="imageInfo" alt="" class="card__img" v-if="imageInfo.length > 0">
      </div>
      </template>

        <h4 class="small-text">{{message}}</h4>
      </div>
      <div class="form__group modifyBox">
         <button @click.prevent="updatePost(selectedPost._id)"  class="btn btn--green">Update</button>
        <button @click.prevent="openDeleteBox" class="btn btn--red">Delete</button>


<div v-if="showDelete" class="deleteBox">
      <h4 class="deleteBox__header">Continue to delete?</h4>
      <div class="deleteBox__deleteButtons">
        <button @click.prevent="cancel" class="btn btn--green">No</button>
  <button @click.prevent="deletePost(selectedPost._id)"  class="btn btn--red">Yes</button>
      </div>
    </div>
      </div>
    </form>

  </div>
</template>
  </div>
</template>

<script>
import axios from 'axios'
import { async } from 'q';
import { mapActions, mapGetters } from 'vuex'
import AdminViewBase from './AdminViewBase'
import { VueEditor } from 'vue2-editor'
import Myslot from './Slots/Slots'
import VueFroala from 'vue-froala-wysiwyg'
export default {

components: {
AdminViewBase,
VueEditor,
Myslot
},


data(){
  return{

  urlPost: '/post/',
 viewPosts: [],
 filteredPost: '',
 postsToUpdate: [],
 selectedComponent: null,
 selectedPost: null,
 viewTitle: 'Published Posts',
 modifyInstructions: 'Click on a post you would like to edit or delete from the list below',
 updateTitle: null,
    updateDescription: null,
    updateContent: null,
    updateDate: null,
    updatedImg: '',
    file: '',
    userId: null,
    search: '',
    showDelete: false,
maxTitle: '40',
maxDescription: '100',
    loading: true,
message: '',
showMyPost: true,
imageInfo: '',
showUploadedImage: true,

config: {
        events: {
          'froalaEditor.initialized': function () {
            console.log('initialized')
          }
        }
      },
  }
},
computed:{
      ...mapGetters({showPosts: 'showMyPost'
      }),
      filterList(){
        if(this.search){
      return this.viewPosts.filter((post)=>{
        return post.postTitle.toLowerCase().startsWith(this.search.toLowerCase());

      })
      }else{
        return this.viewPosts;
      }

      }
},


methods: {

  openDeleteBox(){
    this.showDelete = true;
  },
  cancel(){
this.showDelete = false;
  },
...mapActions({
    ...mapActions({
      changePosts: 'changePosts',
      ChangepostKey: 'ChangepostKey'
    }),
}),
updateImage(){
  this.loading = true;
const file = this.$refs.file.files[0]
this.file = file
if(this.file == file){
  this.loading = false;
}
this.imageInfo = URL.createObjectURL(this.file)
this.showUploadedImage = false;


},
  getSelectedPost(id){
let data = new FormData();
    axios.get(`${this.urlPost}${id}`, {
      params: {
        _id: id
      }
    })
    .then((response) => {
      this.selectedPost = response.data.postDetails,

    this.updateTitle = this.selectedPost.postTitle,
    this.updateDescription = this.selectedPost.postDescription,
    this.updateContent = this.selectedPost.postContent
     this.updateDate = this.selectedPost.dateCreated
     this.updatedImg = this.selectedPost.postObject
console.log(this.updatedImg)

    })
    .catch((err) => {
      console.log(err.message, `${this.urlPost}${id}`)

    })
// this.changeListComponent = false;
this.viewTitle = 'Modify Post'
this.modifyInstructions = 'Update your existing post here by editing the form below'
this.showMyPost = !this.showMyPost
  },

  async updatePost(id){
    let vm = this;
    let myDate = new Date()
this.updateDate = myDate.toDateString()
const formData = new FormData();
formData.append('file', this.file)


formData.append('postTitle',this.updateTitle )
formData.append('postDescription', this.updateDescription  )
formData.append('postContent', this.updateContent )

   await axios.put(`${this.urlPost}${id}`, formData)

    .then((res) => {

this.message = 'Post Updated'


    })
    .catch((err) => {
       console.log(err.message, 'Post Cant be updated ')
       this.message = 'Unable to update post'
    })
this.ChangepostKey();
  },

  showList(){
this.showMyPost = !this.showMyPost
  },

   async deletePost(id){
     for(this.postsToUpdate of this.viewPosts){

     }
 console.log(this.postsToUpdate, 'Before Delete')

    await axios.delete(`${this.urlPost}${id}`)

    .then((res) => {


     this.message = 'Post Deleted'
this.showDelete = false;

    })
    .catch((err) => {
       console.log(err.message)
       this.message = 'Unable to Delete Post'
    })
this.ChangepostKey();
this.showMyPost = !this.showMyPost
  },

},

created(){
console.log('loading');
this.loading = true;
  axios.get(this.urlPost)
.then((response) => {
this.viewPosts = response.data.postDetails
console.log('loaded');
this.loading = false;
})
.catch(err => {
  console.log(err)
})
this.changeListComponent = true;
},

updated(){


}

}
</script>

