<template>
  <div class="selectedBlog">
    <h1 class="selectedBlog__title U-margin-md ">{{myBlog.postDescription}}</h1>

    <div class = "SBox">

 <div class = "SBox__leftBox">
<p class="SBox__info" >Last Updated on: {{myBlog.dateCreated}} by <router-link :to="{name: 'home'}"> SolomonKingSimon </router-link></p>
    <p  v-html="myBlog.postContent" class="SBox__description "></p>
    <!-- <froalaView v-model="myPostContent"></froalaView> -->
    </div>



    <div class = "SBox__rightBox">
<h2  class="SBox__rightBox--header">Latest Posts</h2>

    <div v-for=" listPost of listPosts " :key="listPost._id" class="SBox__container">
<div  class="SBox__container--blogContainer">

  <template v-if="listPost.postObject != undefined || listPost.postObject != null">
      <img :src="listPost.postObject" class="card__img "   alt="" srcset="">
    </template>

    <template v-else>
<img class="card__img "  :src="defaultImg " alt="" srcset="">
    </template>
    </div>
    <div class="SBox__container--list">
<li   @click="goToPost(listPost._id)" class="SBox__container--content">{{listPost.postTitle.substring(0, 25)}}.. </li>
<li class=" small-text SBox__container--content">{{listPost.dateCreated}}</li>
    </div>

    </div>

    </div>

    </div>


  </div>
</template>

<script>
import axios from 'axios'
import { mapGetters, mapActions } from 'vuex';
export default {

  data(){

    return{
Url: '/post/',
listPosts: [],
 defaultImg: 'img/CreateImg.jpg',
 myBlog: null,
    }
  },
  computed:{
    ...mapGetters({
      mySelectedBlog: 'getSelectedBlog'
    })
  },
  methods:{
    ...mapActions({
      modifyBlog: 'modifyBlog'
    }),
async getPosts(){

  this.myBlog = JSON.parse(localStorage.getItem('blogs'));
    await axios.get( this.Url, {
      encoding: null
    })

    .then((res) => {
      this.listPosts = res.data.postDetails
this.listPosts = this.listPosts.reverse();
this.listPosts = this.listPosts.slice(0, 4);

    })
    .catch((err) => {
      console.log('Posts not available')
    })
  },

  goToPost(id){
    axios.get(`${this.Url}${id}`, {
      params: {
        _id: id
      }
    })
    .then((res) => {
this.modifyBlog(res.data.postDetails)
localStorage.setItem('blogs', JSON.stringify(res.data.postDetails))
this.myBlog = JSON.parse(localStorage.getItem('blogs'))
window.scrollTo(0,0)
// this.$router.push({name: 'myBlog'})
    })
    .catch(err =>{
      console.log(err + 'Posts not available')
    })

  },
  },
  created(){
this.getPosts();
  },
  updated(){
    // this.getPosts();
  }


}
</script>

