<template>
  <div class="resetContainer U-margin-top-md">
    <div class="headerBox">
<h1 class=" headerBox__head resetHead">{{resetHeader}}</h1>
    </div>
    <div class="errorBox">
<p v-for="error in errors" :key="error" class=" small-text invalidInputText admin-head">{{error}}</p>
    </div>
<div class="formBox">

<template v-if="showReset">
<form class="form adminForm">

      <div  class="form__group">
        <label for="email" class="form__label heading-5 adminForm__label">Email</label>
        <input :class="{invalidTnputBox: invalidInput}" type="email" v-model="email"  name="email" id="email" class="form__input adminForm__input">

      </div>
      <div class="form__group">
        <button @click.prevent="sendResetLink" class="btn btn--blue-outline">Send Link</button>
      </div>
      <div class="form__group">
        <a @click="goToLogin" class="small-text cursor-pointer">&larr; Remember Password? </a>
      </div>
    </form>
</template>
<template v-else>
<form class="form adminForm">

      <div  class="form__group">
        <label for="email" class="form__label heading-5 adminForm__label">Token</label>
        <input :class="{invalidTnputBox: invalidInput}" type="text" v-model="token"  name="token" id="token" class="form__input adminForm__input">

      </div>
      <div class="form__group">
        <label for="newpassword" class="form__label adminForm__label">Enter New Password</label>
        <input :class="{invalidTnputBox: invalidInput}" type="password" v-model="newPwd"  name="newpassword" id="newpassword" class="form__input adminForm__input">

      </div>
      <div class="form__group">
        <label for="repeatpassword" class="form__label adminForm__label">Repeat Password</label>
        <input :class="{invalidTnputBox: invalidInput}" type="password" v-model="repeatPwd"  name="repeatpassword" id="repeatpassword" class="form__input adminForm__input">

      </div>
      <div class="form__group">
        <button @click.prevent="reset(admin._id)" class="btn btn--blue-outline">Reset Password</button>
      </div>
      <div class="form__group">
        <a @click="goToLogin" class="small-text cursor-pointer">&larr; Remember Password? </a>
      </div>
    </form>
</template>


    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {

components: {

},
data(){
  return{
     email: null,
     invalidInput: false,
     errors: [],
      rejex: /\S+@\S+\.\S+/,
      admin: [],
      showReset: true,
      newPwd: null,
      repeatPwd: null,
      token: null,
      savedToken: null,
      retrievedToken: null,
    mediumRegex: new RegExp("^(((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9])))(?=.{6,})"),
      resetHeader: 'Enter your Email: Reset instructions will be emailed shortly',
      urlReset: '/resetPassword/',
      urlAdmin: '/admin/'

  }
},
methods: {

goToLogin(){
  this.$router.push({name: 'admin'})
},
sendResetLink(){
  this.errors = []



axios.get(this.urlAdmin)
// axios.get('admin/')
        .then((res) => {
          this.admin = res.data.adminDetails

          for (let allAdmin of res.data.adminDetails) {
            this.admin = allAdmin

setTimeout(() => {
  this.invalidInput = false;

}, 1000);

if(!this.email ){
    this.errors.push('Error email field empty')
    this.invalidInput = true
  }

this.email = this.email.toLowerCase();

   if(!this.rejex.test(this.email)){
    this.errors.push('Invalid Email Format')
     this.invalidInput = true
  }

  if( this.email  && this.rejex.test(this.email) && this.email != allAdmin.email ){
    this.errors.push('Email Address doesnt exist')
     this.invalidInput = true
  }

  if (!this.errors.length && this.email === allAdmin.email ) {

    axios.post(this.urlReset, {
      email: this.email
    })
    .then((res) => {
this.showReset = false;
this.resetHeader = 'Reset your password here'
this.savedToken = res.data.token
localStorage.setItem('resetToken', this.savedToken)


    })
    .catch((err) => {
      console.log(err.message),
      this.message= 'Something went wrong'
    })
        return true;
      }
          }
        })
        .catch(err => {
          console.log(err.message)
        })






},

reset(id){
this.errors = []
setTimeout(() => {
   this.invalidInput = false;
}, 1000
);
if (!this.newPwd || !this.repeatPwd || !this.token){
  this.errors.push('Fields must not be blank')
     this.invalidInput = true
}

if(this.newPwd && this.repeatPwd && this.token && this.repeatPwd != this.newPwd){
  this.errors.push('Password Doesnt Match!')
     this.invalidInput = true
}else if(!this.mediumRegex.test(this.newPwd && this.repeatPwd)){
  this.errors.push('Password too weak')
     this.invalidInput = true
}
if(this.token != this.savedToken){
  this.errors.push('Token Invalid')
     this.invalidInput = true
}


if(!this.errors.length && this.token === this.savedToken && this.newPwd === this.repeatPwd){

  axios.put(`${this.urlAdmin}${id}`,{
password: this.newPwd
  })
  .then((res) => {

this.$router.push({name: 'admin'})
  })
  .catch(err => {
    console.log('error', err.message)
  })

}

}
}

}
</script>

